import React from 'react';
import { connect } from "react-redux";
import { setListTasksPage, setNameRoom } from "../../Redux/HeaderReducer";
import { addTask, updateListTasks } from "../../Redux/TasksReducer";
import ListTasks from "./ListTasks";

class ListTasksContainer extends React.Component {
    componentDidMount() {
        this.props.setListTasksPage();
    }
    render() {
        return (
            <ListTasks listTasks={this.props.listTasks} listRooms={this.props.listRooms}
                addTask={this.props.addTask} updateListTasks={this.props.updateListTasks}
                setNameRoom={this.props.setNameRoom}/>
        )
    }
}

let mapstateToProps = (state) => {
    return {
        listTasks: state.tasksPage.listTasks,
        listRooms: state.roomsPage.listRooms
    }
}

export default connect(mapstateToProps, {addTask, updateListTasks, setListTasksPage, setNameRoom})(ListTasksContainer);